body {
  margin: 0;
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}

.leaderboard-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

.leaderboard {
  font-family: "Acme", sans-serif;
  position: absolute;
  top: 46%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 320px;
  height: 66%;
  background: linear-gradient(to bottom, #3a404d, #181c26);
  border-radius: 10px;
  box-shadow: 0 7px 30px rgba(62, 9, 11, 0.3);
}

.leaderboard *,
.leaderboard *::before,
.leaderboard *::after {
  box-sizing: border-box;
}

.leaderboard ul.board-switcher {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-top: -10px;
}

.leaderboard ul.board-switcher li {
  list-style: none;
  display: inline-block;
  width: 38%;
  border: 1px solid #fff;
  padding: 6px;
  color: #fff;
  border-radius: 6px 6px 0 0;
  margin: 0 4px;
  cursor: pointer;
}

.leaderboard ul.board-switcher li.board-active {
  color: #ab0062;
  background-color: #fff;
  font-weight: bold;
  pointer-events: none;
}

.leaderboard p.loading-board {
  text-align: center;
  color: #fff;
  font-style: italic;
  display: none;
}

.leaderboard .board-entries {
  overflow-y: scroll;
  height: calc(100% - 115px);
  margin-top: 10px;
}

.leaderboard .board-entries li mark span.rank {
  position: relative;
  left: -15px;
  background-color: #fff;
  color: red;
  padding: 3px 7px;
  border-radius: 10px;
}

.leaderboard .board-entries li mark img {
  position: absolute;
  width: 33px;
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 6px;
  top: 10px;
}

.leaderboard .board-entries li mark span.name {
  position: relative;
  left: 42px;
}

.leaderboard #closeLeaderboard {
  cursor: pointer;
  position: absolute;
  width: 30px;
  height: 30px;
  right: -13px;
  top: -15px;
}

.leaderboard #closeLeaderboard img {
  background-color: #fff;
  border-radius: 50%;
  padding: 2px;
  opacity: 0.94;
}

.leaderboard object.cancel-svg {
  pointer-events: none;
}

.leaderboard h1 {
  font-size: 18px;
  color: #e1e1e1;
  padding: 0 13px 18px;
  text-align: center;
}

.leaderboard h1 svg {
  width: 25px;
  height: 26px;
  position: relative;
  top: 3px;
  margin-right: 6px;
  vertical-align: baseline;
}

.leaderboard ol {
  counter-reset: leaderboard;
  margin: 0;
  padding: 0;
  list-style: none;
}

.leaderboard ol li {
  position: relative;
  z-index: 1;
  font-size: 14px;
  counter-increment: leaderboard;
  padding: 18px 10px 18px 50px;
  cursor: pointer;
  backface-visibility: hidden;
  transform: translateZ(0) scale(1, 1);
}

.leaderboard ol li mark {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 18px 10px 18px 30px;
  margin: 0;
  background: none;
  color: #fff;
}

.leaderboard ol li mark::before,
.leaderboard ol li mark::after {
  position: absolute;
  z-index: 1;
  bottom: -11px;
  left: -9px;
  border-top: 10px solid #c24448;
  border-left: 10px solid transparent;
  transition: all 0.1s ease-in-out;
  opacity: 0;
}

.leaderboard ol li mark::after {
  left: auto;
  right: -9px;
  border-left: none;
  border-right: 10px solid transparent;
}

.leaderboard ol li small {
  position: relative;
  z-index: 2;
  display: block;
  text-align: right;
  color: #fff;
  font-weight: bold;
  padding-top: 2px;
}

.leaderboard ol li::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ab0062;
  box-shadow: 0 3px 0 rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease-in-out;
  opacity: 0;
}

.leaderboard ol li.is-current-player,
.leaderboard ol li:hover {
  z-index: 2;
  overflow: visible;
}

.leaderboard ol li.is-current-player::after,
.leaderboard ol li:hover::after {
  opacity: 1;
  transform: scaleX(1.06) scaleY(1.03);
}

.leaderboard ol li.is-current-player mark::before,
.leaderboard ol li.is-current-player mark::after,
.leaderboard ol li:hover mark::before,
.leaderboard ol li:hover mark::after {
  opacity: 1;
  transition: all 0.35s ease-in-out;
}

.leaderboard2 {
  display: none;
  top: 86%;
  height: 53px;
}

.leaderboard2 .board-entries {
  height: initial;
  margin-top: 0;
}

.compete {
  display: none;
  margin-top: 40px;
}

.compete img {
  cursor: pointer;
  margin: 0 auto;
  display: block;
  width: 82px;
}

.compete h3 {
  cursor: pointer;
  text-align: center;
  color: #d4d7da;
  position: relative;
  margin-top: -8px;
  margin-left: 10px;
  margin-right: 10px;
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(52, 172, 224, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0);
  }
}

#adCountDown {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  text-align: center;
  display: none;
}

#adCountDown h5,
#adCountDown h3 {
  color: #fff;
  font-family: "Play", sans-serif;
}

#adCountDown h5 {
  margin-top: 10%;
  font-size: 25px;
}

#adCountDown h3 {
  font-size: 60px;
}

#videoAd {
  display: none;
  position: fixed;
  text-align: center;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.94);
  color: #fff;
  font-family: "Acme";
  width: 100%;
  height: 100%;
}

#videoAd h3 {
  margin-top: 45%;
}

#videoAd h4 {
  margin-top: -10px;
  margin-bottom: 34px;
}

#videoAd button {
  background: #32a8d8;
  border: none;
  color: #fff;
  padding: 16px 40px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 48px;
  cursor: pointer;
  outline: inherit;
}

#videoAd button.watch-video {
  animation: pulse 2s infinite;
}

#videoAd button.exit-video {
  margin-top: 26px;
  background: transparent;
  border: 2px solid #eee;
  padding: 3px 26px;
  font-size: 13px;
  opacity: 0.8;
}

